<template>
	<Tabs value="applyin" class="tab">
		<Tab-pane label="押品信息" name="applyin" class="tab-item">
			<div class="mortgager"  v-for="(item,index) in info.mortgageHouseList">
				<div class="order-cell">
					<div class="cell-title">抵押人 -- {{index+1}}</div>
					<div class="cell-body">
						<div class="cell-item">
							<span class="order-item-title">姓名：</span><span>{{item.propertyOwner | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">联系电话：</span><span>{{item.mortgagerTel | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">身份证号：</span><span>{{item.cNumber | empty}}</span>
						</div>
					</div>
				</div>
				<div class="order-cell">
					<div class="cell-title">抵押品信息 -- （房）-- {{item.mHId}}</div>
					<div class="cell-body">
						<div class="cell-item">
							<span class="order-item-title">押品编号：</span><span>{{item.guaranteeNo | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">评估金额：</span><span>{{item.mhouseValue | fmoney | empty }}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">产权证号：</span><span>{{item.propertyRightNumber | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">产权所有人：</span><span>{{item.propertyOwner | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">建筑面积：</span><span>{{item.acreage | empty}}m²</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">押品原值：</span><span>-</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">产权地址：</span><span>{{item.gAddress | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">房屋区域：</span><span>{{item.houseArea | empty}}</span>
						</div>
					</div>
				</div>
				<div class="order-cell" v-if="item.warrant">
					<div class="cell-title">权证信息 -- （房）-- {{item.warrant.warrantId}}</div>
					<div class="cell-body">
						<div class="cell-item">
							<span class="order-item-title">是否完整：</span><span>{{item.warrant.isComplete | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">不完整原因：</span><span>{{item.warrant.incompleteReason| empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">权证类型：</span><span>{{item.warrant.warrantType | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">出证机构：</span><span>{{item.warrant.issueDept | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">出证时间：</span><span>{{item.warrant.issueDate | empty | fDate}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">保管机构：</span><span>{{item.warrant.storageDept | empty}}</span>
						</div>
						<div class="cell-item">
							<span class="order-item-title">住办机构：</span><span>{{item.warrant.handleDept | empty}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="order-cell">
				<div class="cell-title">贷款信息关联</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">借款编号：</span><span>{{info.loanId | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">合同编号：</span><span>-</span>
					</div>
				</div>
			</div>
			<div class="order-cell">
				<div class="cell-title">出入库信息</div>
				<div class="end-body">
					<div class="cell-item detail-cell" v-for="item in info.logManagementList">
						<span>{{item.logOperator | empty}}</span>
						<span>{{item.logDate | empty | fDate('yyyy-MM-dd hh:mm:ss')}}</span>
						<span>【{{item.logOperateType | empty}}】</span>
						<span>备注说明：{{item.logDetails | empty}}</span>
					</div>
				</div>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				info: {}
			}
		},
		created: function () {
			this.loadInfo()
		},
		methods: {
			loadInfo: function () {
				let _this = this;
				let params = _this.$route.params;
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('Storage/newProducInformationByLoanId', {
					loanId: params.loanId,
					serialNo:params.serialNo
				})
					.then(function (response) {
						let res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == '1') {
							_this.$Message.success({
								content: res.msg
							});
							_this.info = res.data;
						} else {
							_this.$Notice.error({
								title: res.msg,
								duration: 3
							});
							_this.$router.go(-1);
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Notice.error({
							title: error,
							duration: 3
						});
						_this.$router.go(-1);
					});
			},
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.order-title {
		text-align: right;
		color: #343434;
		font-size: 14px;
	}

	.order-cell {
		padding-bottom: 5px;
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 30px;
	}

	.cell-end {
		border-bottom: none;
	}

	.cell-title {
		height: 14px;
		border-left: 3px solid #ff8226;
		color: #ff8226;
		font-size: 14px;
		line-height: 14px;
		padding-left: 10px;
		margin-bottom: 30px;
	}

	.cell-body {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.end-body {
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: flex-start;
	}

	.cell-item {
		min-width: 300px;
		padding-bottom: 15px;
		padding-right: 20px;
		color: #333333;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
	}

	.order-item-title {
		width: 100px;
		height: 22px;
		line-height: 22px;
		color: #7f7f7f;
		overflow: hidden;
	}
	.detail-cell>span{
		margin-right: 5px;
	}
</style>



